@use 'variables' as v;

html{
	font-size: 62.5%; //esto nos permite que 1rem sea 16px
	box-sizing: border-box;
}
*,
*:before,
*:after{
	box-sizing: inherit;
}
body{
	font-family: v.$fuente_principal;
	font-size: 1.6rem;

	&.fijar-body{
		overflow: hidden;
	}
}

p{
	font-size: 2rem;
	color: v.$negro;
	line-height: 1.5;
}

.contenedor{
	width: 95%;
	max-width: 120rem;
	margin: 0 auto;
}
 a{
	 text-decoration: none;
 }
 h1, h2, h3 {
	 margin: 0 0 5rem 0;
 }
 h1 {
	 font-size: 4rem;
 }

 h2 {
	 font-size: 3.6rem;
 }

h3 {
	font-size: 3rem;
}
img {
	max-width: 100%;
	display: block;
}